import homePage from "./pages/home_page.vue";
import pageTwo from "./pages/page_two.vue";
import pageThree from "./pages/page_three.vue";
import footerNav from "../../components/tabbar.vue";
import adMin from "./pages/admin.vue";
import {
	ChangePageTitle
} from '../../title.js'
export default {
	data() {
		return {}
	},
	components: {
		homePage,
		pageTwo,
		pageThree,
		footerNav,
		adMin
	},
	computed: {
		page_code() {
			return this.$store.state.footer_store.footer_nav[this.$store.state.footer_store.now_page_index].name_code;
		}
	},
	watch: {
		page_code(newval, oldval) {
			console.log(newval)
			console.log(oldval)
			if (newval == 'home') {
				document.title = '我的跟进记录'
				ChangePageTitle('我的跟进记录')
			} else if (newval == 'apply') {
				document.title = '任务'
				ChangePageTitle('任务')
			} else if (newval == 'my') {
				document.title = '我的'
				ChangePageTitle('我的')
			} else if (newval == 'admin') {
				document.title = '客户'
				ChangePageTitle('客户')
			}
		}
	},
	onLoad() {
		console.log('index onload')
		
	},
	onReachBottom() {
		if (this.page_code == 'home') {
			this.$refs.follow.onReachBottom()
		}
		if (this.page_code == 'admin') {
			this.$refs.admin.onReachBottom()
		}
	},
	methods: {
		open_loading() {
			this.$loading()
		}
	},
	onShow() {
		if (this.page_code == 'home') {
			document.title = '我的跟进记录'
			ChangePageTitle('我的跟进记录')
		} else if (this.page_code == 'apply') {
			document.title = '任务'
			ChangePageTitle('任务')
		} else if (this.page_code == 'my') {
			document.title = '我的'
			ChangePageTitle('我的')
		} else if (this.page_code == 'admin') {
			document.title = '客户'
			ChangePageTitle('客户')
		}
	}

}
